* {
  box-sizing: border-box;
}

.App {
  position: relative;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
}

.compass-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 10;  /* Kompas zawsze na wierzchu */
}

.compass-icon {
  width: 100px;  /* Ustawienie sta≈Çego rozmiaru kompasu */
  height: 100px;
  transition: transform 0.1s ease-in-out;
}

.azimuth-value {
  font-size: 18px;
  margin-top: 5px;
  font-weight: bold;
  color: #333;
}

.map-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.background-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.zoom-controls {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
}

.zoom-controls button {
  padding: 10px 15px;
  font-size: 18px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.zoom-controls button:hover {
  background-color: #555;
}
